<template>
  <form
    class="form-content needs-validation rounded"
    novalidate
    @submit.prevent.stop="handleSubmit"
  >
    <div class="form-content-top rounded-top">
      <div class="form-content-top-header mb-4">
        <h3>
          
        </h3>
        <h5>
           NextMeal 
        </h5>
      </div>
      <div class="form-group">
        <input
          id="name"
          v-model="name"
          type="name"
          class="form-control"
          placeholder=""
          autofocus
          required
        >
        <div class="invalid-feedback">
          
        </div>
      </div>
      <div class="form-group">
        <input
          id="email"
          v-model="email"
          type="email"
          class="form-control"
          placeholder=""
          autofocus
          required
        >
        <div class="invalid-feedback">
          
        </div>
      </div>
      <div class="form-group">
        <input
          id="password"
          v-model="password"
          type="password"
          class="form-control"
          placeholder=""
          minlength="8"
          maxlength="12"
          required
        >
        <div class="invalid-feedback">
           8-12 
        </div>
      </div>
      <div class="form-group">
        <input
          id="passwordCheck"
          v-model="passwordCheck"
          type="password"
          class="form-control"
          placeholder=""
          minlength="8"
          maxlength="12"
          required
        >
        <div class="invalid-feedback">
          {{ validationMsg.passwordCheck }}
        </div>
      </div>
      <div class="btn-container text-center">
        <button
          type="submit"
          class="btn mt-1"
        >
          
        </button>
      </div>
    </div>
    <div class="form-content-bottom rounded-bottom d-flex">
      <p class="text-left m-0 mr-3">
         NextMeal 
      </p>
      <router-link :to="{name: 'login'}">
        
      </router-link>
    </div>
  </form>
</template>

<script>
export default {
  data () {
    return {
      name: '',
      email: '',
      password: '',
      passwordCheck: '',
      validationMsg: {
        passwordCheck: ' 8-12 '
      }
    }
  },
  methods: {
    handleSubmit (e) {
      // Validate form
      const passwordCheckInput = document.getElementById('passwordCheck')

      if (this.password !== this.passwordCheck) {
        passwordCheckInput.setCustomValidity('invalid')
        this.validationMsg.passwordCheck = ''
      } else {
        passwordCheckInput.setCustomValidity('')
        this.validationMsg.passwordcheck = ' 8-12 '
      }

      if (e.target.checkValidity() === false) {
        return e.target.classList.add('was-validated')
      }

      // Send api to check if email already existed
      // Send data to parents
      this.$emit('after-signup', {
        name: this.name,
        email: this.email,
        password: this.password,
        passwordCheck: this.passwordCheck
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.btn {
    @include solidButton(200, 1);
}
</style>
